import React, { useState } from 'react'
import { Grid, GridColumn as Column, GridToolbar } from "@progress/kendo-react-grid";
import orders from "./data/orders.json";
import { process } from '@progress/kendo-data-query';
import '@progress/kendo-theme-material/dist/all.css';
import { ExcelExport } from '@progress/kendo-react-excel-export';
import {IntlService} from '@progress/kendo-react-intl'

function App() {
  const [dataState, setDataState] = useState({
    skip: 0,
    take: 15,
    group: [{ field: 'customerID' }]
  })
  const [result, setResult] = useState(process(orders, dataState))
  const _export = React.useRef(null)
  const _grid = React.useRef();

  const DATE_FORMAT = "yyyy-mm-dd hh:mm:ss.SSS";
  const intl = new IntlService("en");
  orders.forEach((o) => {
    o.orderDate = intl.parseDate(
      o.orderDate ? o.orderDate : "08/28/2020",
      DATE_FORMAT
    );
    o.shippedDate = o.shippedDate
      ? undefined
      : intl.parseDate(
        o.shippedDate ? o.orderDate.toString() : "08/28/2020",
        DATE_FORMAT
      );
  });

  const excelExport = () => {
    let items = orders;
    console.log(result);
    if (result && result.data) {
      items = []
      console.log(result.data.length);
      for (let i = 0; i < result.data.length; i++) {
        for (let k = 0; k < result.data[i].items.length; k++) {
          items.push(result.data[i].items[k]);
        }
      }
    }
    console.log(items);

    if (_export.current !== null) {
      _export.current.save(items, _grid.current.columns);
    }
  }

  const onDataStateChange = (event) => {
    setDataState(event.dataState)
    setResult(process(orders, event.dataState))
  }

  const expandChange = event => {
    const isExpanded = event.dataItem.expanded === undefined ? event.dataItem.aggregates : event.dataItem.expanded;
    event.dataItem.expanded = !isExpanded;
    setDataState({
      ...dataState
    });
  };

  return (
    <div>
      <ExcelExport ref={_export} >
        <Grid
          data={result}
          filterable={true}
          pageable={true}
          groupable={true}
          total={result.length}
          onDataStateChange={onDataStateChange}
          {...dataState}
          expandField="expanded"
          onExpandChange={expandChange}
          ref={_grid}
        >
          <GridToolbar>
            <button
              title="Export Excel"
              className="k-button k-button-md k-rounded-md k-button-solid k-button-solid-primary"
              onClick={excelExport}
            >
              Export to Excel
            </button>
          </GridToolbar>
          <Column field='orderID' title='Order ID' ></Column>
          <Column field='customerID' title='Customer ID' ></Column>
          <Column field='orderDate' title='Order Date' filter='date' format="{0:D}"></Column>
          <Column field='shipName' title='Ship Name'></Column>
        </Grid>
      </ExcelExport>
    </div>
  );
}

export default App;
